/* Web Fonts ----------------------- */

@include font-face('Abolition Regular', abolition-regular);

.main {
    &-heading {
        @include text(5.625rem, 1.3, normal, $color-text-light);
        margin: 12px 0 0;
        text-shadow: 0 1px 1px $color-shadow;
        text-transform: uppercase;
        
        @media #{$brk-narrow} {
            @include text(5rem, 1.1);
        }
    }
    
    &-title {
        @include text(1.625rem, $weight: 200, $color: $color-text-light);
        border-bottom: 2px solid;
        letter-spacing: .065em;
        padding-bottom: 10px;
    
    
        @media #{$brk-narrow} {
            border: none;
            font-size: 1.3rem;
        }
    }
}

.intro {
    @include text(1.25em, 1.6, $color: initial);
    
    @media #{$brk-narrow} {
        font-size: 1rem;
    }
}